<template>
    <div class="MyTest">
        <el-input type="text" v-model="account"> </el-input>
        <el-button @click="login()">发送登录请求</el-button>
        <el-button @click="getRouteAuth()">请求角色权限数据</el-button>
        <el-button @click="updateData()">修改请求</el-button>
    </div>
</template>


<script>
import {LoginAPI} from '@/apis/LoginAPI.js'
import {RouteAuthAPI} from '@/apis/RouteAuthAPI.js'
import { UpdateAPI } from '@/apis/UpdateAPI';
export default {
    name: 'MyTest',
    data() {
        return {
            account:'',
            data:{
                message:'我是新数据!'
            }
        }
    },
    methods: {
        login() {
            const res = LoginAPI(this.account, '123')
            console.log('用户信息:', res)
        },
        getRouteAuth() {
            const res = RouteAuthAPI()
            console.log('权限信息:', res)
        },
        updateData() {
            UpdateAPI(this.data)
        }
    },
}
</script>


